import GiveLike from "@/components/common/GiveLike";
import { SeourceCreatedListDetails } from "@/servers/api";

export default {
  name: "",
  props: ["detailsObj"],
  data() {
    return {
      clickState: 0,
      praiseNum: 0
    };
  },

  components: {
    GiveLike
  },

  computed: {},

  beforeMount() {},

  mounted() {
    this.getDetailsData();
  },

  methods: {
    // 更新点赞状态
    refreshData() {
      this.videoId = this.$route.query.id;
      SeourceCreatedListDetails({
        id: this.$route.query.id,
        type: this.$route.query.type
      })
        .then(res => {
          if (res.data.video) {
            this.clickState = res.data.video.type;
            this.praiseNum = res.data.video.praise_num;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 初始化数据
    getDetailsData() {
      SeourceCreatedListDetails({
        id: this.$route.query.id,
        type: this.$route.query.type
      })
        .then(res => {
          if (res.result === 1) {
            if (res.data.video) {
              this.clickState = res.data.video.type;
              this.praiseNum = res.data.video.praise_num;
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  watch: {}
};



